import React, { Component } from 'react';
import _ from 'lodash';
import Header from '../Header';
import Tools from '../Tools';
import Frames from '../Frames';
import Draw from '../Draw';
import Animation from '../Animation';
import './assets/mainApp.css';

const createEmptyFrame = size => Array.from({ length: size },
  () => Array.from({ length: size }, () => '#a9a9a9'));

const newFrame = createEmptyFrame(32);
const initialState = {
  currentFrameId: 0,
  listFrames: [newFrame],
  frameSize: 32,
  colors: {
    firstColor: '#ffffff',
    secondColor: '#000000',
  },
  tool: 'pen',
};


export default class MainApp extends Component {
  constructor(props) {
    super(props);
    this.state = JSON.parse(localStorage.getItem('state')) || _.cloneDeep(initialState);
    console.log('State: ', this.state);
  }

  // Frames section
  updateFrames = (listFrames) => {
    this.setState(oldState => ({
      ...oldState,
      listFrames,
    }));
  }

  selectCurrentFrame = (id) => {
    this.setState(oldState => ({
      ...oldState,
      currentFrameId: id,
    }));
    console.log('currentFrameId:', id);
  }

  // Tools section
  updateCurrentFrame = (frame) => {
    this.setState((oldState) => {
      const listFrames = [...this.state.listFrames];
      listFrames[this.state.currentFrameId] = frame;
      return {
        ...oldState,
        listFrames,
      };
    });
  }

  selectTool = (tool) => {
    console.log('mainApp tool:', tool);
    this.setState(oldState => ({
      ...oldState,
      tool,
    }));
  }

  // DRAW section
  setColor = (colors) => {
    // colors should be {firstColor: '#fff', secondColor: '#000}
    this.setState(oldState => ({
      ...oldState,
      colors,
    }));
  }

  // point should be {coords: [0, 0], color: '#fff'}
  setStartDrawPoint = (point) => {
    this.setState(oldState => ({
      ...oldState,
      startDrawPoint: point,
    }));
  }

  setFrameSize = (frameSize) => {
    if ((frameSize - this.state.frameSize) === 0) return;

    const originalListFrames = _.cloneDeep(this.state.listFrames);
    const sizedListFrames = [];

    originalListFrames.forEach((frame) => {
      const emptyFrame = createEmptyFrame(frameSize);
      const sizedFrame = emptyFrame.map((row, rowInd) => {
        const rowArr = [...row];
        if (frame[rowInd]) {
          rowArr.splice(0, this.state.frameSize, ...frame[rowInd]);
        }

        rowArr.length = frameSize;
        return rowArr;
      });
      sizedListFrames.push(sizedFrame);
    });

    this.setState(oldState => ({
      ...oldState,
      frameSize,
      listFrames: sizedListFrames,
    }));
  }


  // default action on load and save
  save = () => {
    localStorage.clear();
    localStorage.setItem('state', JSON.stringify(this.state));
  }

  clearLocalStorage = () => {
    this.setState(initialState);
    this.save();
  }

  render() {
    this.save();
    return (
      <div className='main-app'>
        <Header />
        <Tools
          setFrameSize={this.setFrameSize}
          selectTool={this.selectTool}
          selectedTool={this.state.tool}
          colors={this.state.colors}
          setColor={this.setColor}
        />
        <Frames
          frameSize={this.state.frameSize}
          listFrames={this.state.listFrames}
          currentFrameId={this.state.currentFrameId}
          updateFrames={this.updateFrames}
          selectCurrentFrame={this.selectCurrentFrame}
        />
        <Draw
          frameSize={this.state.frameSize}
          currentFrame={this.state.listFrames[this.state.currentFrameId]}
          updateCurrentFrame={this.updateCurrentFrame}
          clearLocalStorage={this.clearLocalStorage}
          selectedTool={this.state.tool}
          colors={this.state.colors}
          setColor={this.setColor}
          startDrawPoint={this.state.startDrawPoint}
          setStartDrawPoint={this.setStartDrawPoint}
          key={`${this.state.currentFrameId}/${this.state.listFrames.length}`}
        />
        <Animation
          frameSize={this.state.frameSize}
          listFrames={this.state.listFrames}
        />
      </div>
    );
  }
}
