import React, { Component } from 'react';

export default class BasicTools extends Component {
  render() {
    return (
      <div className='tools__basic'>
        <button className={`tools__button ${this.props.highlight === 'pen' ? 'tools__highlight' : ''}`}
                value='pen'>Pen</button>
        <button className={`tools__button ${this.props.highlight === 'eraser' ? 'tools__highlight' : ''}`}
                value='eraser'>Eraser</button>
        <button className={`tools__button ${this.props.highlight === 'paintBucket' ? 'tools__highlight' : ''}`}
                value='paintBucket'>PaintB</button>
        <button className={`tools__button ${this.props.highlight === 'paintAllPixels' ? 'tools__highlight' : ''}`}
                value='paintAllPixels'>AllPix</button>
        <button className={`tools__button ${this.props.highlight === 'pipette' ? 'tools__highlight' : ''}`}
                value='pipette'>Pip</button>
        <button className={`tools__button ${this.props.highlight === 'line' ? 'tools__highlight' : ''}`}
                value='line'>Line</button>
        <button className={`tools__button ${this.props.highlight === 'circle' ? 'tools__highlight' : ''}`}
                value='circle'>O</button>
        <button className={`tools__button ${this.props.highlight === 'rectangle' ? 'tools__highlight' : ''}`}
                value='rectangle'>[_]</button>
      </div>
    );
  }
}
