import _ from 'lodash';

export function getAroundPixels(frame, coords, color) {
  const [x, y] = coords;
  const pixelsAround = [];
  for (let startX = x - 1; startX <= x + 1; startX += 1) {
    for (let startY = y - 1; startY <= y + 1; startY += 1) {
      if (frame.length < 1) {
        // eslint-disable-next-line no-continue
        continue;
      }
      if (startX < 0 || startY < 0) {
        // eslint-disable-next-line no-continue
        continue;
      }
      if (startX >= frame[0].length || startY >= frame.length) {
        // eslint-disable-next-line no-continue
        continue;
      }
      if (frame[startY][startX] !== color) {
        // eslint-disable-next-line no-continue
        continue;
      }
      if (x !== startX && y !== startY) {
        if (frame[y][startX] !== color && frame[startY][x] !== color) {
          // eslint-disable-next-line no-continue
          continue;
        }
      }
      pixelsAround.push([startX, startY]);
    }
  }

  return pixelsAround;
}

export function getAllBucketPixels(frame, coords, color) {
  const toCheck = [coords];
  const bucketPixels = new Set([_.join(coords)]);

  while (toCheck.length > 0) {
    const point = toCheck.pop();
    const around = getAroundPixels(frame, point, color);

    around.forEach(pixel => {
      const stringCoords = _.join(pixel);
      if (!bucketPixels.has(stringCoords)) {
        bucketPixels.add(stringCoords);
        toCheck.push(pixel);
      }
    });
  }
  const resp = [];
  const i = bucketPixels.values();

  let x = i.next().value;
  while (x) {
    resp.push(_.split(x, ',').map(Number));
    x = i.next().value;
  }

  return resp;
}
