import React, { Component } from 'react';
import './assets/tools.css';
import CanvasSize from './canvasSize';
import BasicTools from './basicTools';
import ColorPicker from './colorPicker';


export default class Tools extends Component {
  selectTool = (e) => {
    console.log('selectedTool: ', e.target.value);
    this.setState({ selectedTool: e.target.value });

    this.props.selectTool(e.target.value);
  }

  render() {
    return (
      <div className='tools'>
        <CanvasSize setSize={this.props.setFrameSize}/>
        <div className='tools__tool-box' onClick={this.selectTool}>
          <BasicTools highlight={this.props.selectedTool}/>
        </div>
        <ColorPicker colors={this.props.colors} setColor={this.props.setColor}/>
      </div>
    );
  }
}
