import React, { Component } from 'react';

export default class ColorPicker extends Component {

  setFirstColor = (e) => {
    e.persist();
    this.props.setColor({ firstColor: e.target.value, secondColor: this.props.colors.secondColor });
  }

  setSecondColor = (e) => {
    e.persist();
    this.props.setColor({ firstColor: this.props.colors.firstColor, secondColor: e.target.value });
  }


  render() {
    return (
      <div className='tools__color'>
        <input
          className='tools__firstColor'
          type='color'
          value={this.props.colors.firstColor}
          onChange={this.setFirstColor} />
        <input
          className='tools__secondColor'
          type='color'
          value={this.props.colors.secondColor}
          onChange={this.setSecondColor} />
      </div>
    );
  }
}
