import React, { Component } from 'react';
import { Draggable } from 'react-beautiful-dnd';
import DrawCanvas from '../utilites/drawCanvas';
import './assets/single-frame.css';

export default class SingleFrame extends Component {
  constructor(props) {
    super(props);
    this.canvasSize = 128;
    this.frameRef = React.createRef();
  }

  componentDidUpdate() {
    this.updateCanvas();
  }

  componentDidMount() {
    this.updateCanvas();
  }

  updateCanvas = () => {
    DrawCanvas(this.frameRef, this.props.frame, this.canvasSize, this.props.frameSize);
  }

  render() {
    const cls = this.props.highlight ? 'single-frame_canvas highlight' : 'single-frame_canvas';
    return (
      <Draggable draggableId={this.props.index + 1} index={this.props.index} key={this.props.index}>
        {provided => (
          <div className='single-frame' ref={provided.innerRef}
               {...provided.draggableProps} {...provided.dragHandleProps}>
            <canvas
              className={cls}
              onClick={this.props.select(this.props.index)}
              width={this.canvasSize}
              height={this.canvasSize}
              ref={this.frameRef}
            />
            <div className='single-frame_index'>{this.props.index + 1}</div>
            {this.props.framesCount > 1 ? <div className='single-frame_delete' onClick={this.props.delete(this.props.index)}>X</div> : undefined}
            <div className='single-frame_double' onClick={this.props.duplicate(this.props.index)}>x2</div>
          </div>
        )}
      </Draggable>
    );
  }
}
