import _ from 'lodash';

const DrawCanvas = (ref, frame, canvasSize, frameSize) => {
  const pixelSize = Math.round(canvasSize / frameSize);

  const ctx = ref.current.getContext('2d');
  ctx.clearRect(0, 0, canvasSize, canvasSize);
  frame.forEach((row, rowInd) => {
    row.forEach((color, ind) => {
      ctx.fillStyle = color;
      ctx.fillRect(pixelSize * ind, pixelSize * rowInd, pixelSize, pixelSize);
    });
  });
};

export default DrawCanvas;


export function drawLine(startPoint, endPoint) {
  let [x0, y0] = startPoint;
  const [x1, y1] = endPoint;
  const dx = Math.abs(x1 - x0);
  const dy = Math.abs(y1 - y0);
  const sx = (x0 < x1) ? 1 : -1;
  const sy = (y0 < y1) ? 1 : -1;
  let err = dx - dy;

  const resp = [];
  while (true) {
    resp.push([x0, y0]); // Do what you need to for this

    if ((x0 === x1) && (y0 === y1)) break;
    const e2 = 2 * err;
    // eslint-disable-next-line no-param-reassign
    if (e2 > -dy) { err -= dy; x0 += sx; }
    // eslint-disable-next-line no-param-reassign
    if (e2 < dx) { err += dx; y0 += sy; }
  }
  return resp;
}


export function drawCircle(startPoint, endPoint) {
  const [x0, y0] = startPoint;
  const [x1, y1] = endPoint;
  const radius = Math.floor(Math.sqrt(((x1 - x0) ** 2) + ((y1 - y0) ** 2)));
  let f = 1 - radius;
  let ddfX = 0;
  let ddfY = -2 * radius;
  let x = 0;
  let y = radius;

  const resp = [];

  resp.push([x0, y0 + radius]);
  resp.push([x0, y0 - radius]);
  resp.push([x0 + radius, y0]);
  resp.push([x0 - radius, y0]);

  while (x < y) {
    if (f >= 0) {
      y -= 1;
      ddfY += 2;
      f += ddfY;
    }
    x += 1;
    ddfX += 2;
    f += ddfX + 1;

    resp.push([x0 + x, y0 + y]);
    resp.push([x0 - x, y0 + y]);
    resp.push([x0 + x, y0 - y]);
    resp.push([x0 - x, y0 - y]);
    resp.push([x0 + y, y0 + x]);
    resp.push([x0 - y, y0 + x]);
    resp.push([x0 + y, y0 - x]);
    resp.push([x0 - y, y0 - x]);
  }

  return resp;
}


export function drawRect(startPoint, endPoint) {
  const [x0, y0] = startPoint;
  const [x1, y1] = endPoint;
  const resp = [];

  resp.push(...drawLine([x0, y0], [x1, y0]));
  resp.push(...drawLine([x0, y0], [x0, y1]));
  resp.push(...drawLine([x1, y0], [x1, y1]));
  resp.push(...drawLine([x0, y1], [x1, y1]));

  return resp;
}


export function filterPointInsideCanvas(canvasSize, arrayOfPoints) {
  return arrayOfPoints.filter(
    coords => (coords[0] < canvasSize && coords[0] >= 0)
      && (coords[1] < canvasSize && coords[1] >= 0),
  );
}
