import React, { Component } from 'react';
import './assets/canvasSize.css';

export default class CanvasSize extends Component {
  setSize = (e) => {
    console.log('Size: ', e.target.value);
    this.props.setSize(e.target.value);
  }

  render() {
    return (
      <div className='tools__canvas-size' onClick={this.setSize}>
        <button value={32}>32x32</button>
        <button value={64}>64x64</button>
        <button value={128}>128x128</button>
      </div>
    );
  }
}
