import React, { Component } from 'react';
import _ from 'lodash';
import DrawCanvas, {
  drawLine, drawCircle, filterPointInsideCanvas, drawRect,
} from '../utilites/drawCanvas';
import { getAllBucketPixels } from '../utilites/paintBucketTool';
import './assets/draw.css';

const config = {
  line: { func: drawLine },
  circle: { func: drawCircle },
  rectangle: { func: drawRect },
};

export default class Draw extends Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.canvasSize = 639;
    this.state = { coords: [0, 0], shotUpdate: true };
    //this.deb = debounce(this.updateAfterDebounce, 100);
    this.pixelSize = Math.round(this.canvasSize / this.props.frameSize);
  }

  onMouseDown = (e) => {
    this.pixelSize = Math.round(this.canvasSize / this.props.frameSize);
    e.preventDefault();
    this.mouseDown = true;
    this.mouseLived = false;

    const x = Math.floor(e.offsetX / this.pixelSize);
    const y = Math.floor(e.offsetY / this.pixelSize);
    const frame = this.props.currentFrame;
    const color = e.buttons === 1 ? this.props.colors.firstColor : this.props.colors.secondColor;

    this.props.setStartDrawPoint({ coords: [x, y], color });

    if (this.props.selectedTool === 'pen' && e.buttons > 0) {
      frame[y][x] = this.props.startDrawPoint.color;
      this.updateCurrentFrame(frame);
      this.previousPoint = [x, y];
    }

    // if (this.props.selectedTool in config && e.buttons > 0) {
    //   console.log('hi');
    //   //frame[y][x] = this.props.startDrawPoint.color;
    //   this.updateCurrentFrame(frame);
    // }

    if (this.props.selectedTool === 'eraser' && e.buttons === 1) {
      frame[y][x] = '#a9a9a9';
      this.updateCurrentFrame(frame);
    }
  }

  onMouseMove = (e) => {
    this.pixelSize = Math.round(this.canvasSize / this.props.frameSize);
    const x = Math.floor(e.offsetX / this.pixelSize);
    const y = Math.floor(e.offsetY / this.pixelSize);
    const frame = this.props.currentFrame;

    this.setState(oldState => ({
      ...oldState,
      coords: [x, y],
    }));

    if (!this.mouseDown) return;
    if (this.props.selectedTool === 'pen' && e.buttons > 0) {
      const prevPoint = this.previousPoint || [x, y];
      console.log('prevPoint: ', prevPoint);
      const linePixels = drawLine(prevPoint, [x, y]);
      linePixels.forEach((pixel) => {
        frame[pixel[1]][pixel[0]] = e.buttons === 1
          ? this.props.colors.firstColor : this.props.colors.secondColor;
      });
      this.previousPoint = [x, y];
      this.updateCurrentFrame(frame);
    }

    if (this.props.selectedTool === 'eraser' && e.buttons === 1) {
      this.previousPoint = [x, y];
      const linePixels = drawLine(this.previousPoint, [x, y]);
      linePixels.forEach((pixel) => {
        frame[pixel[1]][pixel[0]] = '#a9a9a9';
      });
      this.updateCurrentFrame(frame);
    }

    // draw figures
    if (this.props.selectedTool in config && e.buttons > 0) {
      console.log('tool: ', this.props.selectedTool);
      this.drawFigure([x, y], frame, this.props.startDrawPoint.color);
    }
  }

  updateCurrentFrame(frame) {
    this.props.updateCurrentFrame(frame);
    // this.deb();
  }

  onMouseClick = (e) => {
    e.preventDefault();

    const x = Math.floor(e.offsetX / this.pixelSize);
    const y = Math.floor(e.offsetY / this.pixelSize);
    const frame = this.props.currentFrame;
    const workColor = frame[y][x];

    if (this.props.selectedTool === 'paintBucket' && e.buttons >= 0) {
      console.log('workColor: ', workColor);
      const toColor = getAllBucketPixels(frame, [x, y], workColor);
      toColor.forEach((pixel) => {
        const [x1, y1] = pixel;
        frame[y1][x1] = _.cloneDeep(
          e.buttons === 0 ? this.props.colors.firstColor : this.props.colors.secondColor,
        );
      });
      this.updateCurrentFrame(frame);
    }

    if (this.props.selectedTool === 'paintAllPixels' && e.buttons >= 0) {
      const allPixelsWithSameColor = frame.map((row) => {
        const rowSameColor = row.map((it) => {
          let color = it;
          if (color === workColor) {
            color = e.buttons === 0 ? this.props.colors.firstColor : this.props.colors.secondColor;
          }
          return color;
        });
        return rowSameColor;
      });
      this.updateCurrentFrame(allPixelsWithSameColor);
    }

    if (this.props.selectedTool === 'pipette' && e.buttons >= 0) {
      const { colors } = this.props;
      if (e.buttons === 0) {
        colors.firstColor = workColor;
      } else {
        colors.secondColor = workColor;
      }
      this.props.setColor(colors);
    }
  }

  drawFigure(endPoint, frame, color, save = false) {
    const startPoint = this.props.startDrawPoint.coords;
    let linePixels = config[this.props.selectedTool].func(startPoint, endPoint);
    linePixels = filterPointInsideCanvas(this.props.frameSize, linePixels);
    const newFrame = _.cloneDeep(frame);

    linePixels.forEach((pixel) => {
      console.log('pixel: ', pixel);
      newFrame[pixel[1]][pixel[0]] = color;
    });

    if (save) {
      this.setState(oldState => ({
        ...oldState,
        overlay: undefined,
      }), () => { this.updateCurrentFrame(newFrame); });
    } else {
      this.setState(oldState => ({
        ...oldState,
        overlay: newFrame,
      }));
    }
  }

  onMouseUp = (e) => {
    this.mouseDown = false;
    const x = Math.floor(e.offsetX / this.pixelSize);
    const y = Math.floor(e.offsetY / this.pixelSize);

    console.log('mouseUp:', x, y);
    const frame = this.props.currentFrame;
    if (this.props.selectedTool in config && !this.mouseLived) {
      this.drawFigure([x, y], frame, this.props.startDrawPoint.color, true);
    }
  }

  onMouseLeave = (e) => {
    const x = Math.floor(e.offsetX / this.pixelSize);
    const y = Math.floor(e.offsetY / this.pixelSize);
    const frame = this.props.currentFrame;

    if (this.props.selectedTool in config && this.mouseDown) {
      this.drawFigure([x, y], frame, this.props.startDrawPoint.color, true);
    }
    this.mouseLived = true;
    this.mouseDown = false;

    this.setState(oldState => ({
      ...oldState,
      coords: [0, 0],
    }));
  }

  onDrag = (e) => { e.preventDefault(); }

  componentDidMount() {
    this.ctx = this.myRef.current.getContext('2d');
    this.myRef.current.ondrag = (e) => { e.preventDefault(); };
    this.updateCanvas();

    this.myRef.current.addEventListener('mousedown', this.onMouseDown);
    this.myRef.current.addEventListener('mousemove', this.onMouseMove);
    this.myRef.current.addEventListener('mouseleave', this.onMouseLeave);
    this.myRef.current.addEventListener('mouseup', this.onMouseUp);
    this.myRef.current.addEventListener('click', this.onMouseClick);
    this.myRef.current.addEventListener('contextmenu', this.onMouseClick);
    this.myRef.current.addEventListener('drag', this.onDrag);
  }

  componentWillUnmount() {
    this.myRef.current.removeEventListener('mousedown', this.onMouseDown);
    this.myRef.current.removeEventListener('mousemove', this.onMouseMove);
    this.myRef.current.removeEventListener('mouseleave', this.onMouseLeave);
    this.myRef.current.removeEventListener('mouseup', this.onMouseUp);
    this.myRef.current.removeEventListener('click', this.onMouseClick);
    this.myRef.current.removeEventListener('contextmenu', this.onMouseClick);
    this.myRef.current.removeEventListener('drag', this.onDrag);
  }

  componentDidUpdate() {
    this.updateCanvas();
  }

  updateCanvas = () => {
    DrawCanvas(this.myRef, this.state.overlay || this.props.currentFrame,
      this.canvasSize, this.props.frameSize);
  }

  render() {
    return (
      <div className='draw'>
        <div className='draw__new-sprite' onClick={this.props.clearLocalStorage} >Start new sprite</div>
        <canvas ref={this.myRef} className='draw__canvas' width={this.canvasSize} height={this.canvasSize}/>
        {/* eslint-disable-next-line max-len */}
        <div>[{this.props.frameSize} x {this.props.frameSize}] {this.state.coords[0]} : {this.state.coords[1]}</div>
      </div>
    );
  }
}
