import React, { Component } from 'react';
import _ from 'lodash';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import SingleFrame from './singe-frame';
import './assets/frames.css';


export default class Frames extends Component {
  constructor(props) {
    super(props);
    this.state = { reversed: false };
  }

  addFrame = () => {
    const newFrame = Array.from({ length: this.props.frameSize },
      () => Array.from({ length: this.props.frameSize }, () => '#a9a9a9'));
    this.props.updateFrames([...this.props.listFrames, newFrame]);
    this.props.selectCurrentFrame(this.props.listFrames.length);
  }

  reversFrame = () => {
    this.setState(oldState => ({
      ...oldState,
      reversed: !oldState.reversed,
    }));
  }

  deleteFrame = ind => () => {
    const delFrame = [...this.props.listFrames];
    delFrame.splice(ind, 1);
    if (this.props.currentFrameId > delFrame.length - 1) {
      this.props.selectCurrentFrame(delFrame.length - 1);
    }
    this.props.updateFrames(delFrame);
  }

  duplicateFrame = ind => () => {
    const duplFrame = [...this.props.listFrames];
    duplFrame.splice(ind, 0, _.cloneDeep(duplFrame[ind]));
    this.props.updateFrames(duplFrame);
    this.props.selectCurrentFrame(ind + 1);
  }

  selectFrame = ind => () => {
    this.props.selectCurrentFrame(ind);
  }

  reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
  };

  onDragEnd = (result) => {
    if (!result.destination) {
      return;
    }

    const newOrderedListFrames = this.reorder(
      this.props.listFrames,
      result.source.index,
      result.destination.index,
    );
    this.props.updateFrames(newOrderedListFrames);
    this.props.selectCurrentFrame(result.destination.index);
  }

  render() {
    const { reversed } = this.state;
    const framesArray = reversed ? [...this.props.listFrames].reverse() : this.props.listFrames;
    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable droppableId='droppableId'>
          {provided => (
            <div className='frames'
                 {...provided.droppableProps}
                 ref={provided.innerRef}>
              <div className='frames__buttons'>
                <button className='frames__add-frame' onClick={this.addFrame}>Add frame</button>
                <button className='frames__revers-frame' onClick={this.reversFrame}>Reverse</button>
              </div>
              {framesArray.map(
                (frame, ind) => <SingleFrame
                  key={reversed ? framesArray.length - ind - 1 : ind}
                  index={reversed ? framesArray.length - ind - 1 : ind}
                  frame={frame}
                  frameSize={this.props.frameSize}
                  delete={this.deleteFrame}
                  duplicate={this.duplicateFrame}
                  select={this.selectFrame}
                  framesCount={this.props.listFrames.length}
                  highlight={(reversed ? framesArray.length - ind - 1 : ind) === this.props.currentFrameId}
                />,
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
}
