import React, { Component } from 'react';
import DrawCanvas from '../utilites/drawCanvas';
import './assets/animation.css';

export default class Animation extends Component {
  constructor(props) {
    super(props);
    this.animRef = React.createRef();
    this.canvasSize = 256;
    this.state = { fps: 2 };
  }

  drawOneFrame = (frame) => {
    DrawCanvas(this.animRef, frame, this.canvasSize, this.props.frameSize);
  }

  animate = () => {
    if (this.props.listFrames && this.props.listFrames.length > 0) {
      if (this.props.listFrames[this.step]) {
        this.drawOneFrame(this.props.listFrames[this.step]);
        this.step += 1;
      } else {
        this.drawOneFrame(this.props.listFrames[0]);
        this.step = 1;
      }
    }
    this.animationTimeout = setTimeout(
      () => window.requestAnimationFrame(this.animate), 1000 / this.state.fps,
    );
  }

  componentDidMount() {
    this.animate();
  }

  componentWillUnmount() {
    clearTimeout(this.animationTimeout);
  }

  fpsChangeHandler = (e) => {
    this.setState({ fps: e.target.value });
  }

  render() {
    return <div className='animation'>
      <canvas
        ref={this.animRef}
        className='animation__canvas'
        width={this.canvasSize}
        height={this.canvasSize}
      />
      <input
        className='animation__fps-range'
        onChange={this.fpsChangeHandler}
        id='fps-range'
        value={this.state.fps}
        type='range'
        min={1}
        max={24} />
      <label htmlFor="fps-range">{this.state.fps} FPS</label>
    </div>;
  }
}
